module.exports = "import React, { useState } from \"react\";\nimport {\n  BrowserRouter as Router,\n  Switch,\n  Route,\n  Link,\n  Prompt\n} from \"react-router-dom\";\n\n// Sometimes you want to prevent the user from\n// navigating away from a page. The most common\n// use case is when they have entered some data\n// into a form but haven't submitted it yet, and\n// you don't want them to lose it.\n\nexport default function PreventingTransitionsExample() {\n  return (\n    <Router>\n      <ul>\n        <li>\n          <Link to=\"/\">Form</Link>\n        </li>\n        <li>\n          <Link to=\"/one\">One</Link>\n        </li>\n        <li>\n          <Link to=\"/two\">Two</Link>\n        </li>\n      </ul>\n\n      <Switch>\n        <Route path=\"/\" exact children={<BlockingForm />} />\n        <Route path=\"/one\" children={<h3>One</h3>} />\n        <Route path=\"/two\" children={<h3>Two</h3>} />\n      </Switch>\n    </Router>\n  );\n}\n\nfunction BlockingForm() {\n  let [isBlocking, setIsBlocking] = useState(false);\n\n  return (\n    <form\n      onSubmit={event => {\n        event.preventDefault();\n        event.target.reset();\n        setIsBlocking(false);\n      }}\n    >\n      <Prompt\n        when={isBlocking}\n        message={location =>\n          `Are you sure you want to go to ${location.pathname}`\n        }\n      />\n\n      <p>\n        Blocking?{\" \"}\n        {isBlocking ? \"Yes, click a link or the back button\" : \"Nope\"}\n      </p>\n\n      <p>\n        <input\n          size=\"50\"\n          placeholder=\"type something to block transitions\"\n          onChange={event => {\n            setIsBlocking(event.target.value.length > 0);\n          }}\n        />\n      </p>\n\n      <p>\n        <button>Submit to stop blocking</button>\n      </p>\n    </form>\n  );\n}\n"