module.exports = "import React from \"react\";\nimport {\n  BrowserRouter as Router,\n  Route,\n  Link,\n  Switch,\n  Redirect,\n  useLocation\n} from \"react-router-dom\";\n\n// You can use the last <Route> in a <Switch> as a kind of\n// \"fallback\" route, to catch 404 errors.\n//\n// There are a few useful things to note about this example:\n//\n// - A <Switch> renders the first child <Route> that matches\n// - A <Redirect> may be used to redirect old URLs to new ones\n// - A <Route path=\"*> always matches\n\nexport default function NoMatchExample() {\n  return (\n    <Router>\n      <div>\n        <ul>\n          <li>\n            <Link to=\"/\">Home</Link>\n          </li>\n          <li>\n            <Link to=\"/old-match\">Old Match, to be redirected</Link>\n          </li>\n          <li>\n            <Link to=\"/will-match\">Will Match</Link>\n          </li>\n          <li>\n            <Link to=\"/will-not-match\">Will Not Match</Link>\n          </li>\n          <li>\n            <Link to=\"/also/will/not/match\">Also Will Not Match</Link>\n          </li>\n        </ul>\n\n        <Switch>\n          <Route exact path=\"/\">\n            <Home />\n          </Route>\n          <Route path=\"/old-match\">\n            <Redirect to=\"/will-match\" />\n          </Route>\n          <Route path=\"/will-match\">\n            <WillMatch />\n          </Route>\n          <Route path=\"*\">\n            <NoMatch />\n          </Route>\n        </Switch>\n      </div>\n    </Router>\n  );\n}\n\nfunction Home() {\n  return <h3>Home</h3>;\n}\n\nfunction WillMatch() {\n  return <h3>Matched!</h3>;\n}\n\nfunction NoMatch() {\n  let location = useLocation();\n\n  return (\n    <div>\n      <h3>\n        No match for <code>{location.pathname}</code>\n      </h3>\n    </div>\n  );\n}\n"