module.exports = "import \"./styles.css\";\n\nimport React from \"react\";\nimport {\n  TransitionGroup,\n  CSSTransition\n} from \"react-transition-group\";\nimport {\n  BrowserRouter as Router,\n  Switch,\n  Route,\n  Link,\n  Redirect,\n  useLocation,\n  useParams\n} from \"react-router-dom\";\n\nexport default function AnimationExample() {\n  return (\n    <Router>\n      <Switch>\n        <Route exact path=\"/\">\n          <Redirect to=\"/hsl/10/90/50\" />\n        </Route>\n        <Route path=\"*\">\n          <AnimationApp />\n        </Route>\n      </Switch>\n    </Router>\n  );\n}\n\nfunction AnimationApp() {\n  let location = useLocation();\n\n  return (\n    <div style={styles.fill}>\n      <ul style={styles.nav}>\n        <NavLink to=\"/hsl/10/90/50\">Red</NavLink>\n        <NavLink to=\"/hsl/120/100/40\">Green</NavLink>\n        <NavLink to=\"/rgb/33/150/243\">Blue</NavLink>\n        <NavLink to=\"/rgb/240/98/146\">Pink</NavLink>\n      </ul>\n\n      <div style={styles.content}>\n        <TransitionGroup>\n          {/*\n            This is no different than other usage of\n            <CSSTransition>, just make sure to pass\n            `location` to `Switch` so it can match\n            the old location as it animates out.\n          */}\n          <CSSTransition\n            key={location.pathname}\n            classNames=\"fade\"\n            timeout={300}\n          >\n            <Switch location={location}>\n              <Route path=\"/hsl/:h/:s/:l\" children={<HSL />} />\n              <Route path=\"/rgb/:r/:g/:b\" children={<RGB />} />\n            </Switch>\n          </CSSTransition>\n        </TransitionGroup>\n      </div>\n    </div>\n  );\n}\n\nfunction NavLink(props) {\n  return (\n    <li style={styles.navItem}>\n      <Link {...props} style={{ color: \"inherit\" }} />\n    </li>\n  );\n}\n\nfunction HSL() {\n  let { h, s, l } = useParams();\n\n  return (\n    <div\n      style={{\n        ...styles.fill,\n        ...styles.hsl,\n        background: `hsl(${h}, ${s}%, ${l}%)`\n      }}\n    >\n      hsl({h}, {s}%, {l}%)\n    </div>\n  );\n}\n\nfunction RGB() {\n  let { r, g, b } = useParams();\n\n  return (\n    <div\n      style={{\n        ...styles.fill,\n        ...styles.rgb,\n        background: `rgb(${r}, ${g}, ${b})`\n      }}\n    >\n      rgb({r}, {g}, {b})\n    </div>\n  );\n}\n\nconst styles = {};\n\nstyles.fill = {\n  position: \"absolute\",\n  left: 0,\n  right: 0,\n  top: 0,\n  bottom: 0\n};\n\nstyles.content = {\n  ...styles.fill,\n  top: \"40px\",\n  textAlign: \"center\"\n};\n\nstyles.nav = {\n  padding: 0,\n  margin: 0,\n  position: \"absolute\",\n  top: 0,\n  height: \"40px\",\n  width: \"100%\",\n  display: \"flex\"\n};\n\nstyles.navItem = {\n  textAlign: \"center\",\n  flex: 1,\n  listStyleType: \"none\",\n  padding: \"10px\"\n};\n\nstyles.hsl = {\n  ...styles.fill,\n  color: \"white\",\n  paddingTop: \"20px\",\n  fontSize: \"30px\"\n};\n\nstyles.rgb = {\n  ...styles.fill,\n  color: \"white\",\n  paddingTop: \"20px\",\n  fontSize: \"30px\"\n};\n"