module.exports = "import React from \"react\";\nimport {\n  BrowserRouter as Router,\n  Switch,\n  Route,\n  Link,\n  useHistory,\n  useLocation,\n  useParams\n} from \"react-router-dom\";\n\n// This example shows how to render two different screens\n// (or the same screen in a different context) at the same URL,\n// depending on how you got there.\n//\n// Click the \"featured images\" and see them full screen. Then\n// \"visit the gallery\" and click on the colors. Note the URL and\n// the component are the same as before but now we see them\n// inside a modal on top of the gallery screen.\n\nexport default function ModalGalleryExample() {\n  return (\n    <Router>\n      <ModalSwitch />\n    </Router>\n  );\n}\n\nfunction ModalSwitch() {\n  let location = useLocation();\n\n  // This piece of state is set when one of the\n  // gallery links is clicked. The `background` state\n  // is the location that we were at when one of\n  // the gallery links was clicked. If it's there,\n  // use it as the location for the <Switch> so\n  // we show the gallery in the background, behind\n  // the modal.\n  let background = location.state && location.state.background;\n\n  return (\n    <div>\n      <Switch location={background || location}>\n        <Route exact path=\"/\" children={<Home />} />\n        <Route path=\"/gallery\" children={<Gallery />} />\n        <Route path=\"/img/:id\" children={<ImageView />} />\n      </Switch>\n\n      {/* Show the modal when a background page is set */}\n      {background && <Route path=\"/img/:id\" children={<Modal />} />}\n    </div>\n  );\n}\n\nconst IMAGES = [\n  { id: 0, title: \"Dark Orchid\", color: \"DarkOrchid\" },\n  { id: 1, title: \"Lime Green\", color: \"LimeGreen\" },\n  { id: 2, title: \"Tomato\", color: \"Tomato\" },\n  { id: 3, title: \"Seven Ate Nine\", color: \"#789\" },\n  { id: 4, title: \"Crimson\", color: \"Crimson\" }\n];\n\nfunction Thumbnail({ color }) {\n  return (\n    <div\n      style={{\n        width: 50,\n        height: 50,\n        background: color\n      }}\n    />\n  );\n}\n\nfunction Image({ color }) {\n  return (\n    <div\n      style={{\n        width: \"100%\",\n        height: 400,\n        background: color\n      }}\n    />\n  );\n}\n\nfunction Home() {\n  return (\n    <div>\n      <Link to=\"/gallery\">Visit the Gallery</Link>\n      <h2>Featured Images</h2>\n      <ul>\n        <li>\n          <Link to=\"/img/2\">Tomato</Link>\n        </li>\n        <li>\n          <Link to=\"/img/4\">Crimson</Link>\n        </li>\n      </ul>\n    </div>\n  );\n}\n\nfunction Gallery() {\n  let location = useLocation();\n\n  return (\n    <div>\n      {IMAGES.map(i => (\n        <Link\n          key={i.id}\n          to={{\n            pathname: `/img/${i.id}`,\n            // This is the trick! This link sets\n            // the `background` in location state.\n            state: { background: location }\n          }}\n        >\n          <Thumbnail color={i.color} />\n          <p>{i.title}</p>\n        </Link>\n      ))}\n    </div>\n  );\n}\n\nfunction ImageView() {\n  let { id } = useParams();\n  let image = IMAGES[parseInt(id, 10)];\n\n  if (!image) return <div>Image not found</div>;\n\n  return (\n    <div>\n      <h1>{image.title}</h1>\n      <Image color={image.color} />\n    </div>\n  );\n}\n\nfunction Modal() {\n  let history = useHistory();\n  let { id } = useParams();\n  let image = IMAGES[parseInt(id, 10)];\n\n  if (!image) return null;\n\n  let back = e => {\n    e.stopPropagation();\n    history.goBack();\n  };\n\n  return (\n    <div\n      onClick={back}\n      style={{\n        position: \"absolute\",\n        top: 0,\n        left: 0,\n        bottom: 0,\n        right: 0,\n        background: \"rgba(0, 0, 0, 0.15)\"\n      }}\n    >\n      <div\n        className=\"modal\"\n        style={{\n          position: \"absolute\",\n          background: \"#fff\",\n          top: 25,\n          left: \"10%\",\n          right: \"10%\",\n          padding: 15,\n          border: \"2px solid #444\"\n        }}\n      >\n        <h1>{image.title}</h1>\n        <Image color={image.color} />\n        <button type=\"button\" onClick={back}>\n          Close\n        </button>\n      </div>\n    </div>\n  );\n}\n"